import React, { useState } from 'react';

const ChairAssignModal = ({ open, onClose, onConfirm, defaultName }) => {
  const [name, setName] = useState(defaultName || '');

  if (!open) return null;

  return (
    <div className="modal-backdrop">
      <div className="modal">
        <h3>เลือกชื่อที่จะนั่ง</h3>
        <input
          type="text"
          value={name}
          placeholder="กรอกชื่อ"
          onChange={e => setName(e.target.value)}
        />
        <div style={{ marginTop: 16 }}>
          <button onClick={() => onConfirm(name)} disabled={!name.trim()}>ตกลง</button>
          <button onClick={onClose} style={{ marginLeft: 8 }}>ยกเลิก</button>
        </div>
      </div>
    </div>
  );
};

export default ChairAssignModal;